.container{width:100%;display:flex;flex-direction:column;align-items:center;flex:1 1;margin-bottom:-60px;min-height:100vh;background:#f8fafb}@media (min-width:768px){.container{background-position:center 110px;background-size:100%}}.lang{width:100%;height:40px;line-height:44px;text-align:right}.lang :global(.ant-dropdown-trigger){margin-right:24px}.clean{font-size:18px;margin-top:21px;margin-right:12px}.wrapper{flex:1 1;display:flex;flex-direction:column;padding:32px 0}@media (max-width:480px){.wrapper{padding:8px 0}}.content{min-width:480px;align-items:center;box-shadow:0 24px 36px rgba(0,0,0,.15);padding:48px 64px;overflow:auto;border-radius:4px;background-color:#fff}@media (max-width:768px){.content{min-width:360px;width:95%;padding:24px 28px;align-self:center}}@media (max-width:420px){.content{min-width:auto;width:95%;padding:24px 28px;align-self:center}}.top{text-align:center}.header{height:44px;line-height:44px}.header a{text-decoration:none}.logo{height:44px;margin-right:16px;vertical-align:top}.title{position:relative;top:2px;color:rgba(0,0,0,.85);font-weight:600;font-size:33px;font-family:Avenir,Helvetica Neue,Arial,Helvetica,sans-serif}.desc{margin-top:12px;margin-bottom:40px;color:#98a9bc;font-size:14px}@media (max-width:480px){.desc{margin-bottom:12px;padding:0 12px}}